
:export {
  gap_xsm: 14px;
  gap_sm: 20px;
  gap_md: 30px;
  gap_lg: 44px;
  gap_xlg: 62px;
}
:export {
  size_o_sm: 28px;
  size_o_md: 40px;
  size_o_lg: 56px;
}
:export {
  size_sm: 24px;
  size_md: 34px;
  size_lg: 44px;
}
:export {
  border_radius: 3px;
}
.hiui-price {
  display: inline-block;
}
.hiui-price em {
  font-style: normal;
  font-family: "DINAlternate-Bold", Helvetica;
}
.hiui-price small {
  font-size: 50%;
}
.hiui-price .hiui-price-suffix {
  display: inline-block;
  -webkit-transform: scale(0.9);
     -moz-transform: scale(0.9);
      -ms-transform: scale(0.9);
       -o-transform: scale(0.9);
          transform: scale(0.9);
  margin-left: 3px;
  text-decoration: none;
}
.hiui-price.sup em {
  vertical-align: top;
  line-height: 1;
}
.hiui-price-sup small {
  display: inline-block;
  vertical-align: top;
  line-height: 1.36;
}
.hiui-price-noShrink small {
  font-size: inherit;
  vertical-align: baseline;
}
.hiui-price-delete em,
.hiui-price-delete small {
  text-decoration: line-through;
}


