.hiui-checkbox {
  display: -webkit-inline-box;
  display: -webkit-inline-flex;
  display: -moz-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
     -moz-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: relative;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.hiui-checkbox.disabled {
  opacity: 0.3;
}
.hiui-checkbox.disabled .hidden-input {
  cursor: not-allowed;
}
.hiui-checkbox.active .box {
  border-color: #999;
}
.hiui-checkbox:not(.disabled):hover .box {
  border-color: #999;
}
.hiui-checkbox .box {
  border: 1px solid #ccc;
  width: 15px;
  height: 15px;
  padding: 2px;
  display: inline-block;
  -webkit-transition: border-color 0.3s;
  -o-transition: border-color 0.3s;
  -moz-transition: border-color 0.3s;
  transition: border-color 0.3s;
  vertical-align: text-bottom;
}
.hiui-checkbox .box .item {
  width: 100%;
  height: 100%;
  background-color: #f02f45;
  display: block;
}
.hiui-checkbox .checkbox-label {
  display: inline-block;
  margin-left: 4px;
}
.hiui-checkbox .hidden-input {
  opacity: 0;
  position: absolute;
  cursor: pointer;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.hiui-checkbox-sm {
  font-size: 12px;
}
.hiui-checkbox-sm .box {
  width: 13px;
  height: 13px;
}
.hiui-checkbox-md {
  font-size: 14px;
}
.hiui-checkbox-md .box {
  width: 15px;
  height: 15px;
}
.hiui-checkbox-lg {
  font-size: 16px;
}
.hiui-checkbox-lg .box {
  width: 17px;
  height: 17px;
}
.bounce-enter-active {
  -webkit-animation: bounce-in 0.5s;
     -moz-animation: bounce-in 0.5s;
       -o-animation: bounce-in 0.5s;
          animation: bounce-in 0.5s;
}
.bounce-leave-active {
  -webkit-animation: bounce-out 0.2s;
     -moz-animation: bounce-out 0.2s;
       -o-animation: bounce-out 0.2s;
          animation: bounce-out 0.2s;
}
@-moz-keyframes bounce-in {
0% {
    -moz-transform: scale(0.5);
         transform: scale(0.5);
    opacity: 0;
}
50% {
    -moz-transform: scale(1.05);
         transform: scale(1.05);
}
100% {
    -moz-transform: scale(1);
         transform: scale(1);
    opacity: 1;
}
}
@-webkit-keyframes bounce-in {
0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    opacity: 0;
}
50% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
}
@-o-keyframes bounce-in {
0% {
    -o-transform: scale(0.5);
       transform: scale(0.5);
    opacity: 0;
}
50% {
    -o-transform: scale(1.05);
       transform: scale(1.05);
}
100% {
    -o-transform: scale(1);
       transform: scale(1);
    opacity: 1;
}
}
@keyframes bounce-in {
0% {
    -webkit-transform: scale(0.5);
       -moz-transform: scale(0.5);
         -o-transform: scale(0.5);
            transform: scale(0.5);
    opacity: 0;
}
50% {
    -webkit-transform: scale(1.05);
       -moz-transform: scale(1.05);
         -o-transform: scale(1.05);
            transform: scale(1.05);
}
100% {
    -webkit-transform: scale(1);
       -moz-transform: scale(1);
         -o-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
}
@-moz-keyframes bounce-out {
0% {
    -moz-transform: scale(1);
         transform: scale(1);
    opacity: 1;
}
100% {
    -moz-transform: scale(0.5);
         transform: scale(0.5);
