}
}
@-webkit-keyframes bounce-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
100% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    opacity: 0;
}
}
@-o-keyframes bounce-out {
0% {
    -o-transform: scale(1);
       transform: scale(1);
    opacity: 1;
}
100% {
    -o-transform: scale(0.5);
       transform: scale(0.5);
