.hiui-btn {
  display: inline-block;
  margin-bottom: 0;
  text-align: center;
  white-space: nowrap;
  -webkit-border-radius: 4px;
     -moz-border-radius: 4px;
          border-radius: 4px;
  color: #666;
  border: 1px solid #d9d9d9;
  background-color: #fff;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  position: relative;
  color: rgba(0,0,0,0.65);
  outline: none;
  -webkit-box-shadow: none;
     -moz-box-shadow: none;
          box-shadow: none;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  -moz-transition: all 0.3s;
  transition: all 0.3s;
}
.hiui-btn:not(.disabled):active {
  -webkit-box-shadow: inset 0 2px 3px rgba(0,0,0,0.12);
     -moz-box-shadow: inset 0 2px 3px rgba(0,0,0,0.12);
          box-shadow: inset 0 2px 3px rgba(0,0,0,0.12);
}
.hiui-btn.disabled {
  opacity: 0.3;
}
.hiui-btn-sm {
  padding: 0 12px;
  font-size: 12px;
  height: 24px;
}
.hiui-btn-sm .hiui-icon {
  font-size: 13px;
  vertical-align: top;
}
.hiui-btn-md {
  padding: 0 14px;
  font-size: 14px;
  height: 34px;
}
.hiui-btn-md .hiui-icon {
  font-size: 18px;
  vertical-align: top;
}
.hiui-btn-lg {
  padding: 0 16px;
  font-size: 16px;
  height: 44px;
}
.hiui-btn-lg .hiui-icon {
  font-size: 22px;
  vertical-align: top;
}


