.hiui-toast {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
     -moz-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
       -o-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-border-radius: 3px;
     -moz-border-radius: 3px;
          border-radius: 3px;
  color: #fff;
  z-index: 99999;
  letter-spacing: -0.2em;
  -webkit-box-shadow: 0 5px 15px -5px rgba(0,0,0,0.5);
     -moz-box-shadow: 0 5px 15px -5px rgba(0,0,0,0.5);
          box-shadow: 0 5px 15px -5px rgba(0,0,0,0.5);
  overflow: hidden;
}
.hiui-toast-default {
  background-color: #666;
}
.hiui-toast-default .hiui-icon {
  color: #666;
}
.hiui-toast-primary {
  background-color: #00a0e9;
}
.hiui-toast-primary .hiui-icon {
  color: #00a0e9;
}
.hiui-toast-error {
  background-color: #f02f45;
}
.hiui-toast-error .hiui-icon {
  color: #f02f45;
}
.hiui-toast-warning {
  color: #333;
  background-color: #fac450;
}
.hiui-toast-warning .hiui-icon {
  color: #fac450;
}
.hiui-toast-success {
  background-color: #4fae9b;
}
.hiui-toast-success .hiui-icon {
  color: #4fae9b;
}
.hiui-toast .hiui-icon {
  display: inline-block;
  color: #fff;
  height: 100%;
  margin-left: 10px;
}
.hiui-toast-content {
  display: inline-block;
  padding: 8px 10px;
  letter-spacing: 0;
}
.hiui_fade-enter-active,
.hiui_fade-leave-active {
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  -moz-transition: all 0.3s;
  transition: all 0.3s;
}
.hiui_fade-enter,
.hiui_fade-leave-to {
  opacity: 0;
  -webkit-transform: translate(-50%, -50%) scale(0.9);
     -moz-transform: translate(-50%, -50%) scale(0.9);
      -ms-transform: translate(-50%, -50%) scale(0.9);
       -o-transform: translate(-50%, -50%) scale(0.9);
          transform: translate(-50%, -50%) scale(0.9);
}


