.hiui-modal-wrap {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 999;
}
.hiui-modal-mask {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.8);
  display: table;
  z-index: 1;
}
.hiui-modal-mask:hover+.hiui-modal .hiui-modal-close {
  opacity: 1;
}
.hiui-modal {
  position: absolute;
  z-index: 2;
}
.hiui-modal .hiui-modal-close {
  position: absolute;
  font-size: 32px;
  line-height: 1;
  cursor: pointer;
  opacity: 0.6;
}
.hiui-modal-center {
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
     -moz-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
       -o-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.hiui-modal-center .hiui-modal-close {
  right: 5px;
  top: 5px;
  font-size: 18px;
}
.hiui-modal-top {
  top: 0;
  left: 0;
  width: 100%;
}
.hiui-modal-top .hiui-modal-close {
  right: 5px;
  top: 100%;
  -webkit-transform: translateY(5px);
     -moz-transform: translateY(5px);
      -ms-transform: translateY(5px);
       -o-transform: translateY(5px);
          transform: translateY(5px);
}
.hiui-modal-bottom {
  bottom: 0;
  left: 0;
  width: 100%;
}
.hiui-modal-bottom .hiui-modal-close {
  right: 5px;
  bottom: 100%;
  -webkit-transform: translateY(-5px);
     -moz-transform: translateY(-5px);
      -ms-transform: translateY(-5px);
       -o-transform: translateY(-5px);
          transform: translateY(-5px);
}
.hiui-modal-left {
